      this.getZhuzaoSanData();
      //获取面积图
      this.getAreaData();
      //右上角数据
      this.getRightTopData();

      this.getRightTopLineData();
      this.getRightTopAreaData();

      this.getRightBottomArea1Data();
      this.getRightBottomArea2Data();
    });

    window.onresize = () => {};
  },

  methods: {
    goDetail(n) {
      switch (n) {
        case 1:
          this.$router.push("/zhouzao");
          break;
        case 2:
          this.$router.push("/ronghlian");
          break;
        case 3:
          this.$router.push("/ghdsj8");
          break;
        case 4:
          this.$router.push("/zaoxing");
          break;
        default:
          return;
      }
    },
    //右下角面积图
    getRightBottomArea1Data() {
      echarts.init(document.getElementById("right-bottom")).dispose();
      let paiming = echarts.init(document.getElementById("right-bottom"));
      paiming.setOption(this.option7);
